import React from 'react';
import styled, {keyframes} from 'styled-components';

const WheelChart = ({percent, label}) => {


    const Hole = styled.circle`
        cx: 21;
        cy: 21;
        r: 15.91549430918954;
        fill: white;
    `;

    const Ring = styled.circle`
        cx: 21;
        cy: 21;
        r: 15.91549430918954;
        fill: transparent;
        opacity: 0.5;
        stroke: grey;
        stroke-width: 3;
        stroke-dasharray: 70 30;
        stroke-dashoffset: -40;
    `;

    const rotate = keyframes`to {stroke-dasharray: ${percent * 0.7} ${100   - percent * 0.7};}`;
    const Segment = styled.circle`
        cx: 21;
        cy: 21;
        r: 15.91549430918954;
        fill: transparent;
        stroke: #E75A50;
        stroke-width: 3;
        stroke-dasharray: 0 100;
        stroke-dashoffset: -40;
        animation: ${rotate} 4s ease forwards;
    `;

    const Annotation = styled.text.attrs(
        { x: 21, y: 21}
    )`
        fill: #E75A50;
        font-size: 10px;
        text-anchor: middle;
        alignment-baseline: middle;
        animation: PlanTypeFadeIn 4s;
    
        @keyframes PlanTypeFadeIn {
            from {opacity: 0;}
        }
    `;

    const Label = styled.text.attrs(
        {x: 21, y: 36}
    )`
        fill: grey;
        font-size: 5px;
        text-anchor: middle;
    `;

    return (
        <svg viewBox={`0 0 42 42`}>
            <Hole />
            <Ring />
            <Segment />
            <Annotation>
                {percent}%
            </Annotation>
            <Label>{label}</Label>
        </svg>
    );
}

export default WheelChart;


