import React, {Component} from 'react';
import styled from 'styled-components';
import Card from './Card';
import WheelChart from './WheelChart';

// organize the chart
const Wrapper = styled.div`
display: grid;
grid-template-columns: 1fr 1fr;
`;

const Chart1 = styled.div`
grid-column: 1 / 2;
`;

const Chart2 = styled.div`
grid-column: 2 / 3;
`;

class PlanTypeChart extends Component{
    shouldComponentUpdate(nextProps, nextState) {
        return JSON.stringify(this.props) !== JSON.stringify(nextProps);
    }
   
    render() {
        const {values} = this.props;
        const content = (
            <Wrapper>
                <Chart1>
                    <WheelChart label={'Deluxe'} percent={values.deluxe}/>
                </Chart1>
                <Chart2>
                    <WheelChart label={'Premium'} percent={values.premium}/>
                </Chart2>
            </Wrapper>
        );
        
        return Card("Plan Type Ratio", content);
    }
}

export default PlanTypeChart;


