import React, {Component} from "react";
import SpecialFigure from './SpecialFigure';
import AgeDistribution from "./AgeDistribution";
import PlanTypeChart from './PlanTypeChart';
import MapChart from './MapChart';
import GenderChart from './GenderChart';
import DashboardHeader from './Header';
import {Grid1, Header, Map, Data, Chart, Chart1, Chart2, Chart3} from './gridhelper';
import CommentView from './CommentView';
import SKUSearch from './SKUSearch';
import styled from 'styled-components';
import {Button} from 'react-bootstrap';

//
import fakeData from './fakeData';

// ================ special component ====================
// component here appears in many different components
const ConvertButton = styled(Button)`
    position: absolute;
    right: 5%;
    top: 5%;
    z-index: 50;
`;


export default class Dash  extends Component {
    // ================= states ========================

    constructor(props) {
        super(props);
        this.state = {
            type: 'member',
            showState: 'US',
            noDataError: {display:'none', state: null},
            data: fakeData,
            commentView: false,
            filterDisplay: 'none'
        };
    }

    // ================== Function converts Map to Comment =================
    clickConvertButton = () => {
        this.setState(preState => {
            preState.commentView = !preState.commentView;
            return preState;
        });
    }

    // =================== MapChart Functions =========================

    /* clickState for MapChart
    * click a state then change showState state
    */
   clickState = (state, value) => {
        const helper = (event) => {
            // prevent this click event from invoking the clickBack  
            event.stopPropagation();

            // check if data exists, if not show modal
            if(value === 0) {
                this.setState({noDataError: {display:'block', state: state}});
                return;
            } 

            this.modalCloseClick();
            this.setState(preState => {
                if(preState.showState === state) {
                    preState.showState = 'US';
                } else {
                    preState.showState = state;
                }
                return preState;
            })
        };

        return helper;
    }

    /* clickBack
    * click the background then change showState to US
    */
    clickBack = () => {
        this.setState({showState: 'US'});
    }

    /* modalCloseClick
    * close the modal   
    */
    modalCloseClick = () => {
        this.setState({noDataError: {display:'none', state: null}});
    }



    //======================== SpecialFigure Functions =======================
    /*
    * change the map type
    */
    changeMapType = (type) => {
        const helper = () => {
            this.setState({type: type})
        }
        return helper;
    } 

    //========================== Comment View Functions ===========================
    /*
    * Filter a state in Comment View 
    */
   chooseState = (state) => {
        const clickHandler = (e) => {
            this.setState({showState: state})
        }
        return clickHandler;
   }

   //============================ SKU Filter Functions ============================
   closeFilter = () => {
       this.setState({filterDisplay: 'none'});
       console.log(this.state.filterDisplay);
   }

   openFilter = () => {
        this.setState({filterDisplay: 'block'});
        console.log(this.state.filterDisplay);
   }

    //========================= render =======================================

    render() {
        const {type, showState, noDataError} = this.state;
        const data = this.state.data[showState];
        const {gender, age, planType} = data.statistics;
        const mapchart = (
            <MapChart 
            // pass control function
                clickState = {this.clickState}
                clickBack = {this.clickBack}
                modalCloseClick = {this.modalCloseClick}
            // pass parameters
                type={type}
                showState={showState}
                noDataError={noDataError}
                data={this.state.data}
            />
        );
        const commentview = (
            <CommentView 
                currentState={showState}
                states={Object.keys(this.state.data)}
                chooseState={this.chooseState}
            />
        );
        return(
            <Grid1>
                <SKUSearch closeClick={this.closeFilter} displayState={this.state.filterDisplay}/>
                <Header>
                    <DashboardHeader data={this.state.data['US']} openFilter={this.openFilter}/>
                </Header>
                <Map>
                    <ConvertButton onClick={this.clickConvertButton}>
                        {this.state.commentView ? 'See Map': 'See Comment'}
                    </ConvertButton>
                    {this.state.commentView ? commentview: mapchart}
                </Map>
                <Data>
                    <SpecialFigure 
                        title="Members"
                        icon="user"
                        color="#95D469" //6CDFBE /7bc043
                        value={data.member}
                        unit=""
                        selected={this.state.type==='member'}
                        clickHandler={this.changeMapType('member')}
                    />
                    <SpecialFigure 
                        title="Style Rating"
                        icon="heart"
                        color="#FF7BAC"
                        value={data.style}
                        unit="/5"
                        selected={this.state.type==='style'}
                        clickHandler={this.changeMapType('style')}
                    />
                    <SpecialFigure 
                        title="Fit Rating"
                        icon="scissors"
                        color="#65BAF7"
                        value={data.fit}
                        unit="/1.0"
                        selected={this.state.type==='fit'}
                        clickHandler={this.changeMapType('fit')}
                    />
                </Data>
                <Chart>
                    <Chart1>
                        <GenderChart 
                            values={[gender.boy, gender.girl]}
                        />
                    </Chart1>  
                    <Chart2>
                        <PlanTypeChart values={planType} />
                    </Chart2>     
                    <Chart3>
                        <AgeDistribution
                            values={[age.infant, age.baby, age.young, age.old]}
                        />
                    </Chart3>
                </Chart>
            </Grid1>
        )
    }
};



