import React, {Component} from 'react';
import styled from 'styled-components';
import Card from './Card';
import boy from './pics/boy.png';
import girl from './pics/girl.png';

/* Gender Chart
* use number of figurine pictures to represent the fraction percentage
*/



//=========================== Parameters ================================
// genders picture
const genders = [boy, girl]; 
const colors = [' #65BAF7', '#FF7BAC']; // 
const labels = ['Boys:', 'Girls:'];

// set range for each picture
const ranges = [ 12.5, 25, 37.5, 50, 62.5, 75, 87.5, 100 ];
// animation delay for each picture
const delays = [ '0s', '0.25s', '0.5s', '0.75s', '1s', '1.25s', '1.5s', '1.75s', '2s' ]

//=========================== styled-component ====================================
// grey background
const Icon = styled.div`
    width: 20px;
    height: 40px;
    background: grey;
    position: relative;
    margin: auto;
`;

// color background corresponds to value
const ColorIcon = styled.div`
    width: ${props => props.width * 2 +'px'};
    height: 40px;
    background: ${props => props.color};
    position: absolute;
    animation: men-pop 2s forwards;
    animation-delay: ${props => props.delay}
    opacity: 0;

    @keyframes men-pop {
        to {opacity: 1;}
    }
`;

// picture style, used to get figurine shape
const Outline = styled.img`
    width: 20px;
    height: 40px;
    position: absolute;
    z-index: 50;
`;

// labels style
const Label = styled.td`
    font-size: 2em;
    width: 15%;
    color: ${props => props.color};
    animation: GenderChartFadeIn ${props => props.duration} both;
    animation-delay: ${props => props.delay};

    @keyframes GenderChartFadeIn {
        from {opacity: 0}
        to {opacity: 1}
    }
`;

//============================ Component ================================
// draw gender figurines
const Figurines = ({number, color, gender}) => {

    // calculate color area for each figurine
    const divideNumber = (value) => {
        if(number >= value) {
            return 10;
        } else if(number > value - 10) {
            return number % 10;
        } else {
            return 0;
        }
    }

    return (
        <div style={{display: 'flex', justifyContent: 'space-between'}}>
            {ranges.map((value, index) => (
                <Icon >
                    <ColorIcon width={divideNumber(value)} delay={delays[index]} color={color}/>
                    <Outline src={gender} />
                </Icon>
            ))}
        </div>
    );
}

// add labels to chart
export default class GenderChart extends Component {
    shouldComponentUpdate(nextProps, nextState) {
        return JSON.stringify(this.props) !== JSON.stringify(nextProps);
    }

    render() {
        const {values} = this.props;
        const content = (
            <table style={{width: '100%'}}>
            {values.map((value, index) => 
                    <tr style={{height: '70px'}}>
                        <Label color={colors[index]} delay={'0s'} duration={'0s'}> {labels[index]}</Label>
                        <td><Figurines number={value} color={colors[index]} gender={genders[index]}/></td>
                        <Label color={colors[index]} delay={delays[Math.floor(value / 10)]} duration={'2s'}> {value + '%'}</Label>
                    </tr>
                )}
            </table>
        );
        return Card('Gender Ratio', content);
    }
}





