import React, {Component} from 'react';
import styled from 'styled-components';
import statePaths from './mapOutlineData';
import Modal from './Modal';
import {StateMouseOver} from './ToolTips';

/* Component behavior
* the US states map
* each state shows has a color based on its value, like rating, or population 
* a lengend shows what each color means
* when mouse is over the state, a tooltip shows up
* when click on a state, the brand portal data will change to the state from the whole country
*/

// define the colors for different maps
const MEMBER_COLORS = [ '#95D469', '#AADD87', '#BFE5A5', '#D5EEC3', '#EAF6E1' ]//[ '#E75A50', '#E86E69', '#E97F74', '#E8978F', '#E8B6AF' ];
const STYLE_COLORS = [ '#FF7BAC', '#FF95BD', '#FFB0CD', '#FFCADE' ];
const FIT_COLORS = [ '#65BAF7', '#84C8F9', '#A3D6FA', '#C1E3FC', '#E0F1FD' ];
const NO_DATA_COLOR = '#DFE2D2'; //DFE2D2 /#EDFAFD

// define the value range for different maps
const MEMBER_RANGE = 20;
const RATING_RANGE = 1;

// define the legend indicators for different maps
const MEMBER_INDICATORS = [ '>= 80', '60~79', '40~59', '20~39', '0~19' ];
const RATING_INDICATORS = [ '4~5', '3~4', '2~3', '1~2'];
const FIT_INDICATORS =[ 'Too Big', 'Little Big', 'Perfect', 'Little Small', 'Too Small'];

// define a hashmap which map type to colors, range, indicators
const typesInfo = {
    member: {
        colors: MEMBER_COLORS,
        indicators: MEMBER_INDICATORS,
        range: MEMBER_RANGE,
        description: 'Members',
        title: 'Members Ditribution by State'
    },
    style: {
        colors: STYLE_COLORS,
        indicators: RATING_INDICATORS,
        range: RATING_RANGE,
        description: 'Style Rating',
        title: 'Style Rating in Each State'
    },
    fit: {
        colors: FIT_COLORS,
        indicators: FIT_INDICATORS,
        range: RATING_RANGE,
        description: 'Fit Rating',
        title: 'Fit Rating in Each State'
    }
}

// ==================== functions only used in this components ================

/* color_calculator 
* figures out which color the value belongs to
* value: value need to calculate
* range: value range between two close colors  
* colors: colors array in order
*/
const color_calculator = (value, range, colors) => {
    if(value === 0) {
        return NO_DATA_COLOR;
    } 
    let colorth = colors.length - Math.floor(value / range) - (range === 1? 0: 1); 
    if(colorth >= colors.length) {
        colorth = colors.length-1;
    }
    return colors[colorth];
}

/* opacity_calculator
* figure out the opacity of each state in the map
* state: current state name
* showState: current showing state name
*/
const opacity_calculator = (state, showState) => {
    let normal = 1, hover = 0.5;
    if(showState !== 'US') {
        hover = 1;
        if(showState !== state) {
            normal = 0.2;
        }
    }
    
    return {normal: normal, hover: hover};
}

// ==================== styled-component  ===========================

/* style for each state
* 3 styles
* 1: country: opacity=1, hover:opacity=0.5
* 2: non-country: no-solid: opacity=0.5, hover:opacity=1
* 3: non-country: solid: opacity=1, hover:opacity=1
*/
const RawState = styled.path`
    stroke: white;
    stroke-width: 2px;
    transform: translate(5%, 10%);
    opacity: ${props => props.opacity};

    &:hover {
        opacity: ${props => props.hover};
    }

    &:active {
        fill: white;
    }
`;

/* style for the title in map
*/
const Title = styled.text`
    font-size: 40px;
    fill: ${props => props.color};
`;

/* wrap all components
*/
const Wrapper = styled.div`
    margin: 0;
    padding: 0;
    position: absolute; 
    top: 50%; 
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
`;

/* No data warning */
const Warning = styled.div`
    text-align: center;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: wheat;
    font-size: 2em; 
`;

// =================== Helper Component ======================

/* Legend block
* draw a square filled with specified color with an indicators
* indicator: describes the range for the block
* size: the side length of the square
* color: the filled color
*/
const LegendBlock = ({size, color, indicator}) => (
    <g>
        <rect 
            x={0}
            y={0}
            height={size}
            width={size}
            fill={color}
            fontSize={size/3}
        />
        <text 
            x={-size/3}
            y={size / 2}
            alignmentBaseline={'middle'}
            textAnchor={'end'}
        >
            {indicator}
        </text>
    </g>
)

/* Position 
* put svg group to the prefered position in svg
*/
const Position = (props) => (
    <g transform={"translate(" + props.x + ", " + props.y + ")"}>
        {props.children}
    </g>
)

/* Legend 
* draw a legend with svg
* description: 
* colors: color array 
* indicatiors: title for each block
* size: side length of the block
*/
const Legend =({description, colors, indicators, size}) => (
    <g style={{fontSize: '16px'}}>
        <text 
            x={size/2}
            y={-size / 2}
            textAnchor={'middle'}
            style={{'fontFamily': 'system-ui'}}
        >
            {description}
        </text>
        {indicators.map((indicator, index) => 
            <Position x={0} y={index * size}>
                <LegendBlock 
                    size={size}
                    color={colors[index]}
                    indicator={indicator}
                />
            </Position>
        )} 
        <Position x={0} y={(colors.length+1) * size}>
            <LegendBlock 
                size={size}
                color={NO_DATA_COLOR}
                indicator={'No data'}
            />
        </Position>
    </g>
)


//============== MapChart Component =====================

export default class MapChart extends Component {

    render() {
        const {data} = this.props;
        // Parameters need to be passed by parent component
        const {type, showState, noDataError} = this.props;

        // functions need to be passed by parent component
        const {clickState, clickBack, modalCloseClick} = this.props;

        //extract type information
        const {colors, description, indicators, range, title} = typesInfo[type];

        // function used to draw each stae, write here to reduce parameters
        const drawState = (state) => {

            //check if the state exists in the data, if not give value 0
            const value = data[state.n] == null ? 0: data[state.n][type];

            //calculate the color for the value
            const color = color_calculator(value, range, colors);

            //figure out which style the state should have
            const opacity = opacity_calculator(state.n, showState, value);
            
            // when state is selected, no popup
            if(state.n === showState) { 
                return (
                    <RawState 
                        d={state.d} 
                        fill={color} 
                        opacity={opacity.normal} 
                        hover={opacity.hover}
                        onClick={clickState(state.n, value)}
                        onMouseMove={modalCloseClick}
                    />
                );
            } else {
                return (
                    <StateMouseOver
                        name={state.n}
                        data={data[state.n]}
                    >
                        <RawState 
                            d={state.d} 
                            fill={color} 
                            opacity={opacity.normal} 
                            hover={opacity.hover}
                            onClick={clickState(state.n, value)}
                            onMouseMove={modalCloseClick}
                        />
                    </StateMouseOver>
                );
            }
        }

        return(
            <Wrapper>
                <svg viewBox={`0 0 1200 650`} onClick={clickBack}>
                    <Title color={colors[0]} x={600} y={80} textAnchor={'middle'}>
                        {showState === 'US' ? title: showState}    
                    </Title>
                    <Position x={0} y={30}>
                        {statePaths.map((state) => 
                            drawState(state) 
                        )}
                    </Position>
                    <Position x={1100} y={250}>
                        <Legend 
                            description={description} 
                            colors={colors}
                            indicators={indicators} 
                            size={50} 
                        />
                    </Position>
                </svg>
                <Modal 
                    width={80} 
                    height={20} 
                    closeClick={modalCloseClick}
                    displayState={noDataError.display}
                    backgroundColor={`rgb(0, 0, 0, 0.7)`}
                    closeColor={'white'}
                >
                    <Warning>
                        Sorry, we have no data in {noDataError.state}!
                    </Warning>
                </Modal>
            </Wrapper>
        );
    }
}







