import React, {Component} from 'react';
import styled from 'styled-components';

/* PercentBarChart
* description: barchart shows the percentage of each part
*/

function PercentBarChart({labels, values, width, height}) {
    // color of the bar
    const color = '#95D469'; // #a69ce7

    //space between two bars
    const margin = 10;

    //number of bars
    const n_bars = labels.length;
    
    // separate the map to 6 vertical parts
    const space = height / 6;
    const bar_width = (width - (n_bars + 1) * margin) / n_bars;
    const bar_max_height = space * 5;

    // make sure each bar has enough space on top 
    const top = 1.15 * values.reduce((pre, current) => pre > current ? pre: current);
    const bar_heights = values.map(x => bar_max_height * x/top);

    // calculate horizontal position of each bar
    const x_positions = []; 
    for(let i = 0; i < labels.length; i++) {
        x_positions[i] = (margin + bar_width) * i + margin;
    }
    

//======================= styled-component ============================
    //svg rect used to draw the bar
    const Rect = styled.rect`
        animation: grow 4s ease forwards;

        @keyframes grow {
            from {y: ${bar_max_height}; height: 0;}
        }
    `;

    // svg text used to label the percentage value of each bar
    const BarValueText = styled.text`
        font-size: 5px;
        text-anchor: middle;
        opacity: 0;
        animation: barValueTextFadeIn 2s forwards;
        animation-delay: 2s;

        @keyframes barValueTextFadeIn {
            to {opacity: 1}
        }
    `;

    //======================= component helper =======================
    
    // bar of barchart
    const Bar = ({x, y, height, value}) => (
        <g>
            <Rect
                x={x}
                y={y}
                width={bar_width}
                height={height}
                fill={color}
            />
            <BarValueText
                x={x+bar_width/2}
                y={y-1}
            >
                {value}
            </BarValueText>
        </g>
    );

    // draw horizontal grid
    const left = 0, right = width;
    const grid_heights = [];
    // n_grids, number of grid lines
    const n_grids = 5;
    for(let i = 1; i < n_grids; i++) {
        grid_heights.push(i * space);
    } 
    const GridLine = ({y}) => (
        <line
            x1 = {left}
            y1 = {y}
            x2 = {right}
            y2 = {y}
            key = {y}
            stroke = {'black'}
            stroke-width = {'1px'}
            opacity = {0.1}
        />
    );
    
    //
    const XAxis = () => {
        const y = n_grids * space;
        return (
            <g>
                <line
                    x1 = {left}
                    y1 = {y}
                    x2 = {right}
                    y2 = {y}
                    key = {y}
                    stroke = {'grey'}
                    stroke-width = {'1px'}
                />
                {labels.map((label, index) => 
                <text 
                    x = {x_positions[index] + bar_width / 2}
                    y = {bar_max_height + 5}
                    fontSize = {'5px'}
                    textAnchor = {`middle`}
                >
                    {label}
                </text>
            )}
            </g>
        )
    };

    return (
        <svg viewBox={`0 0 ${width} ${height}`} > 
            {grid_heights.map( y => 
               <GridLine y={y} /> 
            )}

            {bar_heights.map((h, index) => 
                <Bar 
                    x={x_positions[index]}
                    y={bar_max_height - h}
                    width={bar_width}
                    height={h}
                    value={values[index] + '%'}
                />
            )}

            <XAxis />
        </svg>
    );
}

export default PercentBarChart;


