
import styled from 'styled-components';


//============ This file decides the layout of dashboard ================= 

const Grid1 = styled.div`
    display: grid;

    grid-template-areas:
        "header header header header"
        ". . . ."
        "data data data chart"
        "map map map chart"
        ". . . ."
    ;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 10vh 2vh 18vh 68vh 2vh;

    @media (max-width: 1300px) {
        grid-template-areas:
            "header header header"
            "data data data"
            "map map map"
            "chart chart chart"
        ;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 0.3fr 0.5fr 2fr 1fr;

        font-size: 12px;
    }

    font-size: 1.5vh;

    width: 98%;
    margin: auto;
    background-color: #FCFCF5;
`;



const Header = styled.div`
    grid-area: header;
`;

const Map = styled.div`
    grid-area: map;
    margin: -19px 22px 0 22px;
    border: 1px solid grey;
    border-top: 0;
    position: relative;
`;

const Data = styled.div`
    grid-area: data;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
    padding: 20px;
`;

const Chart = styled.div`
    grid-area: chart;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 20px 0 0;

    @media (max-width: 1300px) {
        display: grid;
        grid-template-areas:
            "chart1 chart2 chart3"
        ;
        grid-gap: 10px;
        grid-template-columns: repeat(3, 1fr);
        padding: 20px;
        margin-bottom: 20px;
    }
`;

const Chart1 = styled.div`
    grid-area: chart1;
`;

const Chart2 = styled.div`
    grid-area: chart2;
`;

const Chart3 = styled.div`
    grid-area: chart3;
`;

export {Grid1, Header, Map, Data, Chart, Chart1, Chart2, Chart3};


