import React from 'react';
import styled from 'styled-components';
import {Glyphicon} from 'react-bootstrap';


const Figure = ({title, icon, color, value, unit, selected, clickHandler}) => {
    const arrowSize = 0.2;
    let font = color;
    let background = 'white';
    if(selected) {
        font = 'white';
        background = color;
    }

    // change size of value to small when the value is a string
    let size = '3.5em';
    if(typeof(value) === 'string') {
        size = '2em';
    }

    const Wrapper = styled.div`
        width: 33%;
        height: ${selected? '100%': '65%'};
        border: solid 1px ${selected? background: font};
        border-radius: 5px 5px 0 0;   
        color: ${font};
        background-color: ${background};
        font-size: ${selected? '3.5em': '2em'};
        text-align: center;
        position: relative;

        &:hover {
            ${selected? null: `box-shadow: 0px 8px 8px  rgba(0, 0, 0, 0.2)`};
        }        

        &:after {
            content:"";
            width: 0;
            height: 0;
            position: absolute;
            bottom: -${arrowSize}em;
            left: 50%; 
            margin-left: -${arrowSize/2}em;
            transform: rotate(45deg);
            border: ${arrowSize}em solid;
            border-color: ${background} ${background} ${background} ${background};
            display: ${selected? 'block': 'none'};
            
        }

        &:hover::after {
            content:"";
            width: 0;
            height: 0;
            position: absolute;
            bottom: -${arrowSize}em;
            left: 50%; 
            margin-left: -${arrowSize/2}em;
            transform: rotate(45deg);
            border: ${arrowSize}em solid;
            border-color: ${background} ${background} ${background} ${background};
            ${selected? null:`box-shadow: 8px 8px 8px  rgba(0, 0, 0, 0.2)`};
            display: block;
        }

        &:active {
            ${selected? null: `transform: translateY(5px)`};
        }

    `;

    const Title = styled.h3`
        width: 100%;
        font-size: 0.5em;
        margin: 10px 0;
    `;

    const Icon = styled(
        Glyphicon
    )`
        display: table-cell;
        vertical-align: baseline;
    `;

    const Value = styled.span`
        display: table-cell;
        vertical-align: baseline;
        width: 60%;
    `;

    const Unit = styled.span`
        font-size: 0.5em;
    `;

    const content = (
        <Wrapper onClick={clickHandler}>
            <Title>
                {title}
            </Title>
            <table style={{width: '100%'}}>
                <tr>
                    <Icon glyph={icon}/>
                    <Value style={{fontSize: typeof(value) === 'string'? '0.75em': '1em'}}>
                        {value} <Unit>{unit}</Unit>
                    </Value>
                </tr>
            </table>
        </Wrapper>
    );

    return content;
};

export default Figure;


