import React from 'react';
import styled from 'styled-components';
import Logo from './BrandLogo';
import logo from './pics/anais-i.png';
import {Glyphicon} from 'react-bootstrap';

const Wrapper = styled.div`
    grid-area: header;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    place-self: center;
    border-bottom: 2px #ddd solid;
`;

const LogoImage = styled.div`
    grid-column: 1 / 3;
    padding: 1em 2em;
    box-sizing: border-box;
`;

const Title = styled.div`
    grid-column: 3 / 8;
    place-self: center;
    font-size: 3em;
`;

const TotalFeedback = styled.div`
    grid-column: 8 / 11;
    place-self: end end;
    padding-right: 20px;
    display: flex;
    flex-direction: row;
`;

const Score = styled.span`
    padding: 0 10px;
    color: ${props => props.color};
    font-size: 2em;
    display: block;
`; 

const Header = ({data, openFilter}) => (
    <Wrapper>
        <LogoImage onClick={openFilter}>
            <Logo img={logo} />
        </LogoImage>
        <Title>
            <h1> Brand Portal</h1>
        </Title>
        <TotalFeedback>
            <Score color='#95D469'>
                <Glyphicon glyph={'user'} /> {data.member}
            </Score>
            <Score color='#FF7BAC'>
                <Glyphicon glyph={'heart'} /> {data.style}
            </Score>
            <Score color='#65BAF7'>
                <Glyphicon glyph={'scissors'} /> {data.fit}
            </Score>
        </TotalFeedback>
    </Wrapper>
);

export default Header;


