import React from 'react';
import styled from 'styled-components';
import {Tooltip, OverlayTrigger, Table} from 'react-bootstrap';

const StyledTable = styled(Table)`
    > td {
        text-align: right;
    }
    
    > th {
        text-align: left;
    }
`;

const Header = styled.div`
    text-align: center;

    > strong {
        font-size: 1.5em;
    }
`;

const StyledTooltip = styled(Tooltip)`

    > .tooltip-inner {
        font-size: 1.5em;
        min-width: 200px;
    }
`;

const stateData = (name, data) => (
    <StyledTooltip>
        <Header>
            <strong> {name} </strong>
            <p>{data.member} members</p>
        </Header>
        <StyledTable>
            <tbody>
            <tr>
                <th>
                    Style
                </th>
                <td>
                    {data.style}
                </td>
            </tr>
            <tr>
                <th>
                    Fitting
                </th>
                <td>
                    {data.fit}
                </td>
            </tr>
            </tbody>
        </StyledTable>
    </StyledTooltip>
)

const stateNoData = (name) => (
    <StyledTooltip>
        <Header>
            <strong>{name}</strong>
            <p>No Data!</p>
        </Header>
    </StyledTooltip>
)

const StateMouseOver = ({name, data, children}) => {
    let tooltip = null;
    if(data) {
        tooltip = stateData(name, data);
    } else {
        tooltip = stateNoData(name);
    }
    
    return (
        <OverlayTrigger
            placement={'right'}
            overlay={tooltip}
        >
            {children}
        </OverlayTrigger>
    );
}

export {StateMouseOver};


