import React from 'react';
import BarChart from './PercentBarChart.js';
import Card from './Card.js';

// ==================== Parameters =====================
const width=100; 
const height=50; 
const labels=['6mon-2y', '2y-6y', '6y-12y', '12y-14y'];

class AgeDistribution extends React.Component {
    
    // prevent rendering when nothing changes
    shouldComponentUpdate(nextProps, nextState) {
        return JSON.stringify(nextProps) !== JSON.stringify(this.props);
    } 

    render() {
        const {values} = this.props;
        const content = (
            <BarChart
                labels={labels}
                values={values}
                width={width}
                height={height}
            />
        );
        return Card('Member Distribution by Age', content);
    }
}

export default AgeDistribution; 


