import React, {Component} from 'react';
import Modal from './Modal';
import {Pagination} from 'react-bootstrap';
import styled from 'styled-components';

const types = ['Top', 'Bottom', 'Dress', 'One-Piece'];
const arr = [ 0, 1, 2, 3, 4, 5, 6, 7];

const Wrapper = styled.div`
    display: flex;
    flex-direction: row;
    margin: 30px;
    border: 2px solid white;
    height: 94%;
`; 

const Categories = styled.div`
    width: 200px;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    border-right: solid 2px #ddd;
`;

const Category = styled.div`
    font-weight: bold;
    font-size: 1.2em;
    margin: 10px;
`;

const Label = styled.label`
    margin-left: 25px;
    font-weight: normal;
    > span {
        margin-left: 10px;
    }
`;

const Checkbox = ({type}) => (
    <Label>
        <input type='checkbox'></input>
        <span>{type}</span>
    </Label>
);

const Gallery = styled.div`
    flex: 1;
    border: 2px grey solid;
`;

const Inventories = styled.div`
    height: 80%;
    margin-top: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 5px;
`;

export default class SKUSearch extends Component {
    constructor(props) {
        super(props);
        this.gallery = React.createRef();
        this.state = {

        };
    }

    render() {
        
        return (
            <Modal
                width={90}
                height={90}
                displayState={this.props.displayState}
                closeClick={this.props.closeClick}
                closeColor={'black'}
                backgroundColor={'white'}
            >
            <Wrapper>
                <Categories>
                    <Category> Clothing </Category>
                    {types.map(type => 
                        <Checkbox type={type} />
                    )}
                </Categories>
                <Gallery ref={this.gallery} >
                    <Inventories onClick={console.log(this.gallery)}>
                        {arr.map(num => (
                            <figure>
                                <img 
                                    src='https://images-na.ssl-images-amazon.com/images/I/81qjhBjiEkL._AC_SR201,266_.jpg' 
                                    style={{width: '198px', height: '300px'}}
                                />
                                <figcaption>{num}</figcaption>
                            </figure>
                        ))}
                    </Inventories>
                    <div style={{width: '100%', margin: 'auto'}}>
                    <Pagination>
                        <Pagination.First />
                        <Pagination.Prev />
                        <Pagination.Item>{1}</Pagination.Item>
                        <Pagination.Ellipsis />

                        <Pagination.Item>{10}</Pagination.Item>
                        <Pagination.Item>{11}</Pagination.Item>
                        <Pagination.Item active>{12}</Pagination.Item>
                        <Pagination.Item>{13}</Pagination.Item>
                        <Pagination.Item disabled>{14}</Pagination.Item>

                        <Pagination.Ellipsis />
                        <Pagination.Item>{20}</Pagination.Item>
                        <Pagination.Next />
                        <Pagination.Last />
                    </Pagination>
                    </div>
                </Gallery>
            </Wrapper>
            </Modal>
        );
    }
}


