const CommentList = styled.ul`
    width: 90%;
    margin-top: 10px;
    height: 85%;
    overflow-y: auto;
    border: solid 1px #eeeeee;
`;

const CommentItem = styled.li`
    margin-top: 20px;
`;

const FilterBar = styled.div`
    display: flex;
    justify-content: flex-start;
`;

const Filter = styled.div`
    > p {
        font-weight: bold;
    }

    margin-right: 20px;
`;

const reviews = ['All reviews', 'Positive reviews', 'Negative reviews'];

const CommentView = (props) => (
    <div style={{position: 'absolute', top: '5%', left: '5%', bottom:'3%'}}>
        <FilterBar>
            <Filter>
                <p>Comments in:</p>
                <Dropdown>
                    <Dropdown.Toggle
                        style={{minWidth: '150px'}}
                    >
                        {props.currentState}
                    </Dropdown.Toggle>
                    <Dropdown.Menu 
                        style={{maxHeight: '250px', overflowY: 'auto'}}
                    >
                        {props.states.map(state => 
                            <MenuItem
                                eventKey={state}
                                onClick={props.chooseState(state)}
                            >
                                {state}
                            </MenuItem>
                        )}
                    </Dropdown.Menu>
                </Dropdown>
            </Filter>
            <Filter>
                <p>Pick up by: </p>
                <Dropdown>
                    <Dropdown.Toggle
                        style={{minWidth: '150px'}}
                    >
                        All reviews
                    </Dropdown.Toggle>
                    <Dropdown.Menu 
                        style={{maxHeight: '250px', overflowY: 'auto'}}
                    >
                        {reviews.map(review => 
                            <MenuItem
                                eventKey={review}
    
                            >
                                {review}
                            </MenuItem>
                        )}
                    </Dropdown.Menu>
                </Dropdown>
            </Filter>
        </FilterBar>
        <CommentList>
            {comments.map(comment => (
                <CommentItem>
                    {comment}
                </CommentItem>
            ))}
        </CommentList>
    </div>
)

export default CommentView;


