import React, {Component} from 'react';
import styled from 'styled-components';

function Card(title, content, border='withborder') {

    const OutBorder = styled.div`
        border: solid ${props => props.onborder == 'noborder' ? (0 + 'px'): (2 +'px')}  #c0c5ce;
        border-radius: 5px;
        padding: 0;
        background: white;
        height: 100%;
    `;

    const InnerBorder = styled.div`
        margin: 0 2%;
    `;

    const Title = styled.div`
        color: #343d46;
        font-size: 1.5em;
        font-weight: bold;
        font-family: "Source Sans Pro Black";
        text-align: center;
    `;

    const SplitLine = styled.hr`
        border-color:  #c0c5ce;
        margin-top: 0px;
    `;

    const Content = styled.div`
        font-family: "Gill Sans", sans-serif;
    `;
    

    return (
        <OutBorder onborder={border}>
            <InnerBorder>
                <Title>{title}</Title>
                <SplitLine />
                <Content>{content}</Content>
            </InnerBorder>
        </OutBorder>
    );
}


export default Card;



