import React from 'react';
import styled from 'styled-components';
import {Glyphicon} from 'react-bootstrap';

/*Modal component
* closeClick parent pass function to change the display of Modal to none
* displayState, state passed from parent to 
*/

const Modal = ({width, height, closeClick, displayState, children, backgroundColor, closeColor}) => {
    const top = 50 - height/2;
    const left = 50 - width/2;
    const Block = styled.div.attrs({
        width: width, 
        height: height,
        top: top,
        left: left
    })`
        position: fixed;
        top: ${props => props.top}%;
        left: ${props => props.left}%;
        z-index: 100;
        width: ${props => props.width}%;
        height: ${props => props.height}%;
        background: ${backgroundColor};
        border: 2px solid grey;
    `;
    // font-size must equal to width/height
    const CloseIcon = styled.div`
        position: absolute;
        right: 5px;
        top: 5px;
        width: 25px;
        height: 25px;
        color: ${closeColor}
        font-size: 25px;
        background: transparent;
        border: 0;
        z-index: 1001;
        &:hover {
            opacity: 0.7;
        }
    `;
    
    const Content = styled.div`
        width: 100%;
        height: 100%;
        position: relative;
    `;

    return (
        <Block style={{display: displayState}}>
            {console.log('display:' + displayState)}
            <CloseIcon onClick={closeClick}>
                <Glyphicon glyph='remove-circle'/>
            </CloseIcon>
            <Content>
                {children}
            </Content>
        </Block>
    );
} 

export default Modal;


